.section{
  &-hero{
    &__wrapper{
      height: 1010px;

      background: url("../img/hero-bg.svg") center bottom/cover no-repeat;

      padding: 120px 0 0 0;
    }
    &__grid{
      display: grid;
      grid-template-columns: repeat(2,minmax(0,1fr));
      gap: 16px;
      &-item{
        display: flex;
        flex-direction: column;
        justify-content: center;
      }
    }
    &__desc{
      margin: 40px 0 0 0;
    }
    &__text{
      font-family: 'Gilroy', sans-serif;
      font-weight: 400;
      font-size: 24px;
      line-height: 34px;

      color: #FFFFFF;
    }
  }
}

@media screen and (max-width: 767px) {
  .section{
    &-hero{
      &__wrapper{
        height: 730px;
        background: url("../img/hero-bg-m.svg") center bottom/cover no-repeat;

        padding: 60px 0 0 0;
        position: relative;

        overflow: hidden;
      }
      &__grid{
        grid-template-columns: minmax(0,1fr);
      }
      &__desc{
        margin: 16px 0 0 0;
      }
      &__text{
        font-size: 16px;
        line-height: 19px;
      }
      &__img{
        position: absolute;
        height: 400px;
        width: 420px;

        left: calc(50% - 210px);
        bottom: 50px;
        &-item{
          width: 100%;
          height: 100%;

          object-fit: contain;
        }
      }
    }
  }
}