.catalog{
  &-theme{
    &__wrapper{
      max-width: 910px;
      width: 100%;
    }
    &__content{
      margin: 16px 0 0 0;
    }
  }
  &-search{
    &__wrapper{
      display: grid;
      grid-template-columns: 275px 600px;
      gap: 50px;
      &-block + &-block{
        margin: 28px 0 0 0;
      }
    }
    &__block{
      &-header + &-content{
        margin: 8px 0 0 0;
      }
    }
  }

}

.theme{
  &-list{
    display: flex;
    flex-wrap: wrap;
    margin: -6px -12px;
    &__item{
      margin: 6px;

      background: #FFE9B1;
      border-radius: 50px;

      overflow: hidden;
      &.active{
        background: #FFA319;
      }
    }
    &__link{
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;

      padding: 14px 40px;

      display: flex;
      align-items: center;
      justify-content: center;

      background: inherit;
    }
  }
}

@media screen and (max-width: 991px) {
  .catalog{
    &-search{
      &__wrapper{
        grid-template-columns: minmax(0,1fr);
        &-block{
          &:nth-child(2){
            display: none;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .theme{
    &-list{
      &__link{
        padding: 12px 32px;
      }
    }
  }
}