.filter{
  &__wrapper{
    &-block + &-block{
      border-top: 1px solid #D9D9D9;
    }
    &-title{
      font-family: 'Gilroy', sans-serif;
      font-weight: 700;
      font-size: 28px;
      line-height: 30px;
      text-align: center;

      color: #6776FF;
    }
    &-header{
      margin: 0 0 6px 0;

      display: none;

      position: relative;
    }
    &-tags{
      display: none;

      margin: 24px 0;
    }
    &-close{
      position: absolute;
      right: 33px;
      top: 16px;

      width: 32px;
      height: 32px;
      align-items: center;
      justify-content: center;

      background: #F8F8FF;
      border-radius: 40px;

      display: none;
      &:hover{
        cursor: pointer;
      }
      &-img{
        width: 10px;
        height: 10px;

        object-fit: contain;
      }
    }
  }
  &__list{
    display: flex;
    flex-wrap: wrap;

    margin: -6px -12px;
    &-item{
      margin: 6px;
    }
    &-link{
      padding:8px 12px;

      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 14px;

      color: #2F2F2F;

      display: flex;
      align-items: center;

      background: #EDEDED;
      border-radius: 6px;
      &::after{
        content: '';
        width: 10px;
        height: 10px;
        display: block;

        margin: 0 0 0 12px;
        background: url("../img/close.svg") center center/contain no-repeat;
      }
      &-reset{
        background: #E3E3E3;
        &::after{
          display: none;
        }
      }
    }
  }
  &-block{
    &__wrapper{
      padding: 18px 0;
      &.active{
        padding: 18px 0 24px 0;
      }
    }
    &__header{
      display: flex;
      align-items: center;
      justify-content: space-between;

      &:hover{
        cursor: pointer;
      }

      &::after{
        content: '';
        width: 19px;
        height: 19px;
        display: block;

        background: url("../img/arrow-black.svg") center center/contain no-repeat;

        transform: rotate(180deg);

        transition: .3s transform linear;
      }
      &.active{
        &::after{
          transform: rotate(0);
        }
      }
    }
    &__content{
      margin: 14px 0 0 0;

      display: none;
      &.active{
        display: block;
      }
    }
    &__search{
      margin: 0 0 16px 0;
    }
  }
  &-checkbox{
    &__list{
      &-item{
        & + &{
          margin: 8px 0 0 0;
        }
      }
    }
  }
}

.checkbox{
  &__wrapper{
    display: flex;
    align-items: center;
    &:hover{
      cursor: pointer;
    }
  }
  &__input{
    display: none;
    &:checked + .checkbox__checkmark{
      background: #6776FF;
      border-color: #6776FF;

      &::before{
        opacity: 1;
      }
    }
  }
  &__checkmark{
    display: flex;
    align-items: center;
    justify-content: center;

    background: #FFFFFF;
    border: 1px solid #C0C6FF;
    border-radius: 2px;

    width: 22px;
    height: 22px;

    position: relative;

    transition: .3s border-color, .3s background-color;
    &::before{
      content: '';
      position: absolute;
      left: 0;
      top: 0;

      width: 100%;
      height: 100%;

      background: url("../img/check-white.svg") center center/contain no-repeat;

      opacity: 0;

      transition: .3s opacity linear;
    }
  }
  &__label{
    margin: 0 0 0 10px;

    font-family: 'Graphik LCG', sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
  }
}

.search{
  &-input{
    &__wrapper{
      position: relative;

      &::after{
        content: '';
        position: absolute;
        right: 16px;
        top: calc(50% - 12px);

        width: 24px;
        height: 24px;
        display: block;

        background: url("../img/search-icon.svg") center center/contain no-repeat;
      }
    }
    &__item{
      width: 100%;
      height: 42px;

      outline: none;

      border: 1px solid #C0C6FF;
      border-radius: 10px;

      padding: 0 40px 0 16px;

      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 14px;

      color: #2F2F2F;

    }
  }
}

.vendor{
  &-list{
    &-wrapper{
      max-height: 300px;
      overflow-x: hidden;
      overflow-y: auto;

      margin: 8px 0 0 0;
    }
    &-link{
      display: flex;
      align-items: center;

      font-family: 'Gilroy', sans-serif;
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;

      color: #6776FF;

      margin: 16px 0 0 0;
      &::after{
        content: '';
        width: 12px;
        height: 12px;
        display: block;
        flex-shrink: 0;

        margin-left: 6px;

        background: url("../img/arrow-down-blue.svg") center center/contain no-repeat;

        transition: .3s all linear;
      }
    }
  }
}

.expandable{
  &-list{
    &-item{
      &.hidden{
        display: none;
      }
    }
    &-link{
      &.active{
        &::after{
          transform: rotate(180deg);
        }
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .filter{
    &__wrapper{
      &-header{
        display: block;
      }
      &-tags{
        display: block;
      }
      &-close{
        display: flex;
      }
    }
    &__list{
      margin: -2px -4px;
      &-item{
        margin: 4px;
      }
      &-link{
        padding: 4px 6px;

        font-size: 12px;

        border-radius: 4px;
      }
    }
  }
}