.comment{
  &-item{
    &__wrapper{
      border: 2px solid #6776FF;
      border-radius: 20px;

      padding: 24px;

      width: 100%;
    }
    &__header{
      display: flex;
      justify-content: space-between;
    }
    &__text{
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;

      color: #2F2F2F;
    }
    &__link{
      display: flex;
      align-items: center;


      font-family: 'Gilroy', sans-serif;
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;

      color: #2F2F2F;

      margin: 16px 0 0 0;
      &::after{
        content: '';
        width: 12px;
        height: 12px;
        display: block;

        flex-shrink: 0;

        background: url("../img/arrow-black.svg") center center/contain no-repeat;
        transform: rotate(180deg);

        margin: 0 0 0 8px;

        transition: .3s transform linear;
      }
      &.active{
        &::after{
          transform: rotate(0);
        }
      }
    }
    &__profile{
      display: flex;
      align-items: stretch;
      &-img{
        width: 40px;
        height: 40px;

        border-radius: 50%;
        overflow: hidden;

        flex-shrink: 0;
        img{
          width: 100%;
          height: 100%;

          object-fit: cover;
        }
      }
      &-img + &-content{
        margin: 0 0 0 28px;
      }
      &-content{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
      }
      &-name{
        font-family: 'Gilroy', sans-serif;
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;

        color: #2F2F2F;
      }
      &-text{
        font-family: 'Graphik LCG', sans-serif;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;

        color: #777777;
      }
    }
    &__header  + &__content{
      margin: 16px 0 0 0;
    }
  }
  &-slider{
    &__item{
      max-width: 620px;
      width: 100%;
    }
    &__footer{
      display: flex;
      align-items: center;

      justify-content: space-between;

      max-width: 620px;
      width: 100%;
    }
    &-pagination{
      font-family: 'Gilroy', sans-serif;
      font-weight: 700;
      font-size: 22px;
      line-height: 27px;

      color: #6776FF;
    }
    &-button{
      &-next,&-prev{
        left: unset;
        right: unset;
        top: unset;

        position: relative;


        width: 70px;
        height: 32px;

        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;

        background-image: url("../img/slider-arrow.svg");

        margin: 0;
      }
      &-prev{
        transform: rotate(180deg);
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .comment{
    &-item{
      &__profile{
        flex-direction: column;
        &-img + &-content{
          margin: 10px 0 0 0;
        }
      }
    }
  }
}