.section{
  &-about{
    &__wrapper{
      padding: 80px 0 160px 0;

    }
    &__grid{
      display: grid;
      grid-template-columns: repeat(2,minmax(0,1fr));
      gap: 100px;

      padding: 0 115px;
    }
    &__title{
      font-family: 'Gilroy', sans-serif;
      font-weight: 700;
      font-size: 45px;
      line-height: 60px;
    }
    &__desc{
      margin: 4px 0 0 0;
    }
    &__text{
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 20px;
      line-height: 26px;
    }
    &__footer{
      margin: 50px 0 0 0;
    }
    &__img{
      width: auto;
      height: 280px;
      &-item{
        width: 100%;
        height: 100%;

        object-fit: contain;
      }
    }
  }
}

@media screen and (max-width: 1280px) {
  .section{
    &-about{
      &__grid{
        padding: 0;
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .section{
    &-about{
      &__grid{
        grid-template-columns: minmax(0,1fr);
        gap: 70px;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .section{
    &-about{
      &__wrapper{
        padding: 30px 0 112px 0;
      }
      &__title{
        font-size: 32px;
        line-height: 38.4px;
      }
      &__desc{
        margin: 8px 0 0 0;
      }
      &__text{
        font-size: 16px;
        line-height: 19px;
      }
      &__footer{
        margin: 25px 0 0 0;
      }
    }
  }
}