.lesson{
  &-wrapper{
    &__block{
      padding: 32px 40px 40px 32px;

      background: #F8F8F8;
      border-radius: 25px;
    }
    &__title{
      font-family: 'Gilroy', sans-serif;
      font-weight: 700;
      font-size: 24px;
      line-height: 29px;

      color: #6776FF;
    }
    &__content{
      & + &{
        border-top: 1px solid #B6B6B6;

        padding:40px 0 0 0;
      }
      display: grid;
      grid-template-columns: 290px auto;
      &-big{
        grid-template-columns: 250px auto;
      }
    }
  }
  &-list{
    &__item{
      & + &{
        margin: 16px 0 0 0;
      }
    }
  }
  &-item{
    &:nth-child(1){
      .lesson-item__wrapper{
        padding-top: 18px;
      }
    }

    & + &{
      border-top: 1px solid #B6B6B6;
    }
    &__wrapper{
      padding: 34px 0 34px 40px;
    }
    &__desc{
      margin: 12px 0 0 0;
    }
    &__text{
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;

      color: #2F2F2F;
    }
    &__icon{
      flex-shrink: 0;

      width: 27px;
      height: 27px;

      transition: .3s transform linear;
      &-img{
        width: 100%;
        height: 100%;

        object-fit: contain;
      }
    }
    &__intro{
      display: flex;
      align-items: flex-start;

      padding: 8px 0 0 0;
      &-icon{
        margin: 0 0 0 40px;

        flex-shrink: 0;
      }
    }
    &__header{
      display: flex;
      align-items: center;
      position: relative;
      &.active{
        .lesson-item__icon{
          transform: rotate(180deg);
        }
      }
      &-title{
        max-width: 535px;

        margin: 0 auto 0 0;
      }
      &-info{
        margin: 0 0 0 15px;
      }
      &-icon{
        margin: 0 0 0 40px;
      }
      &-bonus{
        position: absolute;
        top: calc(50% - 20px);
        left: -132px;
      }
    }
    &__footer{
      margin: 24px 0 0 0;
    }
    &__button{
      max-width: 260px;
      width: 100%;
    }
    &__info{
      display: flex;
      align-items: center;
      &-block{
        & + &{
          margin: 0 0 0 32px;
        }
      }
    }
    &__title{
      font-family: 'Gilroy', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;

      color: #2F2F2F;
      &-bold{
        font-weight: 700;
      }
    }
    &__content{
      margin: 16px 0 0 0;

      max-width: 740px;

      display: none;
      &.active{
        display: block;
      }
      &-grid{
        display: grid;
        grid-template-columns: 415px auto;
        gap: 17px;
      }
    }
    &__video{
      width: 100%;
      height: 234px;

      &-block{
        border-radius: 10px;
        width: 100%;
        height: 100%;

        object-fit: cover;
      }
    }
    &__download{
      display: flex;
      align-items: center;
      &-link{
        font-family: 'Graphik LCG', sans-serif;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #6776FF;

        display: flex;
        align-items: center;
        &::before{
          content: '';
          width: 17px;
          height: 17px;
          display: block;

          background: url('../img/download.svg') center center/contain no-repeat;

          margin: 0 8px 0 0;
        }
      }
      &-text{
        font-family: 'Graphik LCG', sans-serif;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;

        color: #B6B6B6;

        margin: 0 0 0 10px;
      }
    }
    &__reviews{
      margin: 24px 0 0 0;
    }
  }
  &-form{
    &__footer{
      display: flex;
      align-items: flex-start;
      justify-content: space-between;

      margin: 16px 0 0 0;
    }
    &__button{
      width: 180px;
    }
  }
}

@media screen and (max-width: 1024px) {
  .lesson{
    &-wrapper{
      &__content{
        grid-template-columns: minmax(0,1fr);
        &-item{
          &_expanded + &{
            margin: 24px 0 0 0;
          }
          &.trigger-block{
            &.active{
              display: none;
            }
          }
        }
      }
    }
    &-item{
      &:nth-child(1){
        .lesson{
          &-item{
            &__wrapper{
              padding-top: 24px;
            }
          }
        }
      }
      &__wrapper{
        padding: 24px 0;
      }
      &__header{
        flex-wrap: wrap;
        &-title{
          flex: 100%;
          order: 3;
          width: 100%;
          max-width: 100%;

          margin: 16px 0 0 0;
        }
        &-info{
          margin: 0 0 0 auto;
        }
        &-info ~ &-icon{
          margin: 0 0 0 24px;
        }
        &-icon{
          margin: 0 0 0 auto;
        }
        &-bonus{
          position: relative;
          left: 0;
          top: 0;
        }
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .lesson{
    &-item{
      &__content{
        &-grid{
          grid-template-columns: minmax(0,1fr);
          gap: 16px;
        }
      }
      &__info{
        &-block{
          &-desktop{
            display: none;
          }
        }
      }
    }
    &-wrapper{
      &__block{
        padding: 24px;
      }
      &__content{
        &-title{
          position: relative;
          display: flex;
          align-items: center;
          justify-content: space-between;
          &::before{
            content: '';
            position: absolute;
            height: 80px;
            width: calc(100% + 48px);
            top: -24px;
            left: -24px;

            background: #ECECEC;
            border-radius: 20px;
            z-index: 0;
          }
          &::after{
            content: '';
            position: relative;

            width: 30px;
            height: 30px;

            background: url("../img/arrow-black-second.svg") center center/contain no-repeat;
          }
          .title{
            position: relative;
            z-index: 2;
          }
        }
      }
    }
    &-form{
      &__footer{
        flex-direction: column;
        &-block{
          & +& {
            margin: 32px 0 0 0;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .lesson{
    &-item{
      &__video{
        height: 140px;
      }
      &__intro{
        &-icon{
          display: none;
        }
      }
    }
  }
}