.card{
  &__wrapper{
    padding: 20px 24px;
    border-radius: 16px;
    background-color: #fff;

    display: flex;
    flex-direction: column;
    height: 240px;
    &:hover{
      .card{
        &__content{
          opacity: 1;
        }
        &__logo{
          animation: showCard .5s linear forwards;

          opacity: 1;
        }
        &__difficult{
          animation: hideCard .5s linear forwards;
        }
        &__bonus{
          animation: hideCard .5s linear forwards;
        }
      }
    }
  }
  &__header{
    display: flex;
    align-items: flex-start;
    &_center{
      align-items: center;
    }
    &-block{
      & + &{
        margin-left: 30px;
      }
      &_time{
        flex-shrink: 0;
      }
    }
  }
  &__title{
    font-family: 'Gilroy', sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
  }
  &__time{
    font-family: 'Graphik LCG', sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;

    color: #6776FF;

    flex-shrink: 0;
  }
  &__content{
    margin:12px 0 auto 0;

    opacity: 0;

    transition: .5s opacity linear;
    &_big{
      margin: 21px 0 auto 0;
    }
    &_visible{
      opacity: 1;
    }
  }
  &__text{
    font-family: 'Graphik LCG', sans-serif;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    &_second{
      font-family: 'Gilroy', sans-serif;
      font-weight: 600;
      font-size: 14px;
      line-height: 17px;
    }
  }
  &__footer{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;

    height: 30px;
    &_full{
      height: 100%;
    }
    &-text{
      & + &{
        margin: 8px 0 0 0;
      }
    }
  }
  &__logo{
    height: 24px;

    opacity: 0;

    transition: .3s all;
    &-item{
      width: 100%;
      height: 100%;

      object-fit: contain;
    }
  }
  &__img{
    width: 100px;
    height: 22px;

    object-fit: contain;
  }
  &__button{
    max-width: 130px;
    height: 40px;
  }
  &__passed{
    display: flex;
    align-items: center;
    &-icon{
      width: 24px;
      height: 24px;

      svg{
        width: 100%;
        height: 100%;
      }
    }
    &-text{
      margin: 0 0 0 8px;

      font-family: 'Gilroy', sans-serif;
      font-weight: 600;
      font-size: 16px;

      color: #FFA319;
    }
  }
}

.bonus{
  &__wrapper{
    width: 112px;
    height: 40px;

    background: #FFA319;
    border-radius: 21px;

    display: flex;
    align-items: center;
    justify-content: center;
    &::before{
      content: '';
      width: 33px;
      height: 33px;
      display: block;

      background:url("../img/bonuse.svg") center center/contain no-repeat;

      margin-right: 4px;
    }
    &-small{
      width: 72px;
      &::before{
        display: none;
      }
    }
  }
  &__text{
    font-family: 'Gilroy', sans-serif;
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
    color: #FFFFFF;
  }
}

.difficult{
  &__wrapper{
    display: grid;
    grid-template-columns: repeat(3,minmax(0,36px));
    grid-auto-rows: 16px;
    gap: 2px;

    border-radius: 8px;
    overflow: hidden;

    width: max-content;
    &_big{
      grid-template-columns: repeat(3,minmax(0,87px));
      grid-auto-rows: 24px;
    }
    &_medium{
      .difficult{
        &__item{
          &_low{
            background-color: #C6D1FF;
          }
          &_medium{
            background-color: #A5B1FF;
          }
          &_hard{
            background-color: #EDEDED;
          }
        }
      }
    }
    &_low{
      .difficult{
        &__item{
          &_low{
            background-color: #C6D1FF;
          }
          &_medium{
            background-color: #EDEDED;
          }
          &_hard{
            background-color: #EDEDED;
          }
        }
      }
    }
    &-item{
      &.active{
        position: relative;
        &::before{
          content: '';

          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 3px;
          display: block;

          background-color: #FFA319;
        }
      }
    }
  }
  &__item{
    height: 100%;
    &_low{
      background: #C6D1FF;
    }
    &_medium{
      background: #A5B1FF;
    }
    &_hard{
      background: #6776FF;
    }
  }
}

@keyframes hideCard {
  50%{
    opacity: 0;
  }
  100%{
    opacity: 0;
    display: none;
  }
}

@keyframes showCard {
  50%{
    opacity: 1;
  }
  100%{
    opacity: 1;
    display: block;
  }
}

@media screen and (max-width: 767px) {
  .card{
    &__wrapper{
      padding: 20px;
    }
  }
}