.section{
  &-event{
    &__wrapper{
      padding: 122px 0 50px 0;
    }
    &__content{
      margin: 8px 0 0 0;
    }
    &__list{
      display: none;
    }
    &__footer{
      margin: 40px 0 0 0;

      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}

.event{
  &-list{
    &__item{
      background: #FFFFFF;
      box-shadow: 0px 0px 15px rgba(7, 14, 77, 0.1);
      border-radius: 16px;
      & + &{
        margin: 8px 0 0 0;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .section{
    &-event{
      &__content{
        margin: 40px 0 0 0;
      }
      &__wrapper{
        padding: 112px 0;
      }
      &__calendar{
        display: none;
      }
      &__list{
        display: block;
      }
    }
  }
}