.training {

  &-header {
    &__title {
      margin: 16px 0 0 0;
    }

    &__info {
      margin: 40px 0 0 0;
    }

    &__favorite {
      margin: 64px 0 0 0;

      display: flex;
      align-items: flex-start;

      &--active {
        .training-header__favorite-btn {
          padding: 0;

          align-self: center;
        }
      }

      &-btn {
        width: 255px;

        margin: 0 0 0 48px;

        padding: 24px 0 0 0;

        .button {
          width: 100%;
        }
      }
    }

    &__label{
      &-text{
        font-family: 'Graphik LCG', sans-serif;
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;

        color: #2F2F2F;

      }
    }

    &__description {
      margin: 8px 0 0 0;

      max-width: 600px;

      &-text {
        font-family: 'Graphik LCG', sans-serif;
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        color: #2F2F2F;
      }
    }
  }

  &-banner {
    &__wrapper {
      display: flex;
      justify-content: space-between;
      height: 185px;

      &-img {
        width: 370px;
        height: 270px;

        flex-shrink: 0;

        transform: translateY(-47px);

        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }

      &-content {
        margin: 0 0 0 140px;

        align-self: center;

        max-width: 330px;
        flex-shrink: 0;
      }

      &-title {
        font-family: 'Gilroy', sans-serif;
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;
      }

      &-button {
        margin: 16px 0 0 0;

        box-shadow: 0px 4px 6px rgba(0, 35, 103, 0.08);
        width: 255px;
      }

      &-social {
        margin-left: auto;

        align-self: flex-end;

        padding-bottom: 40px;
      }
    }

    &__social {
      &-title {
        font-family: 'Graphik LCG', sans-serif;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;

        color: #2F2F2F;
      }

      &-list {
        display: flex;
        align-items: center;
        justify-content: flex-end;

        margin: 17px 0 0 0;
      }

      &-item {
        & + & {
          margin: 0 0 0 17px;
        }

        display: block;
        width: 45px;
        height: 45px;
      }
    }
  }

  &-form {
    &__content {
      margin: 24px 0 0 0;
    }

    &__textarea {
      height: 130px;
    }

    &__footer {
      margin: 32px 0 0 0;
    }

    &__mark {
      margin: 24px 0 0 0;

      &-content {
        margin: 8px 0 0 0;
      }
    }

    &__description{
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;

      color: #2F2F2F;
    }
  }

  &-question {
    &__list {
      display: flex;
      flex-direction: column;

      &-item {
        & + & {
          margin: 12px 0 0 0;
        }
      }
    }

    &__item {
      &-wrapper {

        border: 2px solid #FFE9B1;
        border-radius: 16px;
      }

      &-header {
        display: flex;
        align-items: center;
        justify-content: space-between;

        border-radius: 13px;
        background: #FFE9B1;

        padding: 12px 24px 14px 24px;

        flex-shrink: 0;

        &::after {
          content: '';

          width: 30px;
          height: 30px;
          display: block;

          transform: rotate(180deg);

          transition: .3s transform linear;

          flex-shrink: 0;

          background: url("../img/arrow-black.svg") center center/contain no-repeat;
        }

        &:hover {
          cursor: pointer;
        }

        &.active {
          &::after {
            transform: rotate(0);
          }
        }
      }

      &-title {
        font-family: 'Gilroy', sans-serif;
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;

        color: #2F2F2F;
      }

      &-content {
        padding: 16px 24px 24px 24px;

        display: none;

        &.active {
          display: block;
        }
      }
    }
  }

  &-img {
    width: 100%;
    height: 100%;

    position: relative;
    z-index: 2;

    &__progress {
      width: 100%;
      height: 8px;

      background: #EAEAEA;

      border-radius: 100px;
      overflow: hidden;

      position: relative;
    }

    &__info {
      display: flex;
      flex-direction: column;
      height: 100%;

      &-text {
        font-family: 'Graphik LCG', sans-serif;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;

        color: #2F2F2F;
      }

      &-title {
        font-family: 'Gilroy', sans-serif;
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;

        color: #2F2F2F;
      }

      &-content {
        margin: 12px 0 0 0;
      }

      &-footer {
        margin: 16px 0 0 0;
      }

      &-button {
        margin: auto 0 0 0;
      }

      &-certificate{
        margin:  auto 0 0 0;
      }
    }

    &__certificate {
      display: flex;
      align-items: flex-end;
      justify-content: space-between;

      img {
        width: 155px;
        height: 110px;

        object-fit: contain;
      }
    }
  }

  &-compound{
    &__title{
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: #FFFFFF;
    }
    &__content{
      margin: 10px 0 0 0;
    }
    &__grid{
      display: grid;
      grid-template-columns: repeat(2,minmax(100px,240px));
      gap: 10px 18px;
    }
    &__item{
      display: flex;
      align-items: center;
      &-icon{
        width: 28px;
        height: 28px;
      }
      &-content{
        margin: 0 0 0 15px;
      }
      &-text{
        font-family: 'Graphik LCG', sans-serif;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;

        color: #FFFFFF;
      }
    }
  }

  &__header {
    &-wrapper {
      display: grid;
      grid-template-columns: auto 415px;

      gap: 126px;
    }

    &-img {
      width: 100%;
      height: 240px;

      border: 2px solid #6776FF;
      border-radius: 20px;

      display: flex;
      align-items: center;
      justify-content: center;

      padding: 5px 58px;

      &_small {
        padding: 24px 36px;
      }

      &_mobile {
        display: none;
      }

      &_end {
        position: relative;
        overflow: hidden;

        &::before {
          content: '';

          position: absolute;
          right: -10px;
          top: 0px;

          width: 200px;
          height: 160px;
          display: block;

          background: url("../img/medal.svg") center center/contain no-repeat;
        }
      }

      &_progress {
        position: relative;
        overflow: hidden;

        &::before {
          content: '';

          position: absolute;
          right: -10px;
          top: 0px;

          width: 200px;
          height: 160px;
          display: block;

          background: url("../img/sand-big.svg") center center/contain no-repeat;
        }
      }

      img {
        width: 100%;
        height: 100%;

        object-fit: contain;
      }
    }
  }

  &__banner {
    margin: 120px 0 0 0;

    &-wrapper {
      background: #F9F9FF;
    }
  }

  &__comments {
    margin: 104px 0 0 0;

    &-content {
      margin: 32px 0 0 0;
    }

    &-footer {
      margin: 24px 0 0 0;
    }

    &-wrapper {
      &_small {
        max-width: 620px;
      }
    }
  }

  &__grid {
    display: grid;
    grid-template-columns: auto 524px;
    gap: 124px;
  }

  &__faq {
    margin: 140px 0 0 0;

    &-content {
      margin: 10px 0 0 0;
    }
  }

  &__lessons{
    margin: 96px 0 0 0;
  }
}

.training {
  &-info {
    &__wrapper {
      display: flex;
      align-items: center;

      &-small{
        max-width: 600px;
      }
      &-btn{
        & + &{
          margin: 24px 0 0 0;
        }
      }
      &-block {
        display: flex;
        align-items: center;

        & + & {
          margin: 0 0 0 48px;
        }
        & + &_buttons{
          margin: 0 0 0 auto;
        }
        &_start{
          align-self: flex-start;
        }
      }
    }

    &__icon {
      width: 24px;
      height: 24px;
    }

    &__content {
      margin: 0 0 0 12px;
    }

    &__text {
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
    }
  }

  &-tags {
    &__wrapper {
      display: flex;
      align-items: center;

      &-item {
        & + & {
          margin: 0 0 0 76px;
        }
      }
    }
  }

  &-tag {
    &__wrapper {
      display: flex;
      align-items: flex-start;
    }

    &__icon {
      width: 33px;
      height: 33px;
      flex-shrink: 0;
    }

    &__content {
      margin: 0 0 0 12px;
    }

    &__title {
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;

      color: #2F2F2F;
    }

    &__text {
      margin: 7px 0 0 0;

      font-family: 'Gilroy', sans-serif;
      font-weight: 700;
      font-size: 16px;
      line-height: 20px;
    }
  }

  &-description {
    &__block {
      &-content {
        margin: 16px 0 0 0;
      }

      &-text {
        margin: 32px 0 0 0;
      }
    }
  }

  &__tags {
    margin: 105px 0 0 0;
  }

  &__description {
    margin: 72px 0 0 0;
  }

  &__desc {
    font-family: 'Gilroy', sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;

    color: #2F2F2F;

    max-width: 738px;
  }

  &__text {
    font-family: 'Graphik LCG', sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;

    color: #2F2F2F;

    &-small {
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      max-height: 160px;

      max-width: 630px;
    }

    p + p {
      margin: 20px 0 0 0;
    }

    ul {
      list-style: disc;

      margin-left: 25px;
    }
  }

  &__info{
    margin: 40px 0 0 0;
    &-grid{
      display: grid;
      grid-template-columns: repeat(2,minmax(0,1fr));
      grid-auto-rows: 126px;
      gap: 20px;
    }
    &-item{
      padding: 24px 32px;
      border-radius: 25px;
      height: 100%;
      width: 100%;
      &_yellow{
        background: #FFA319;
      }
      &_blue{
        background: #6776FF;
      }
      &_small{
        padding: 16px 32px;
      }
    }
    &-progress{
      width: 100%;
      height: 12px;
      position: relative;

      background: #FFFFFF;
      border-radius: 100px;
    }
  }

  &-bonuses{
    &__header{
      display: flex;
      align-items: center;
    }
    &__text{
      margin: 0 0 0 17px;
    }
    &__count{
      margin: 0 0 0 20px;
    }
    &__img{
      width: 45px;
      height: 45px;

      img{
        width: 100%;
        height: 100%;

        object-fit: contain;
      }
    }
    &__content{
      margin: 15px 0 0 0;
    }
    &__count{
      display: flex;
      align-items: flex-end;
      &-text{
        margin: 0 0 0 17px;
      }

    }
  }
}

.favorite {
  &-block {
    &__wrapper {
      height: 146px;
      width: 414px;

      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      padding: 24px 40px;

      border-radius: 20px;
      overflow: hidden;

      background: url("../img/favorite-bg.svg") center center/cover no-repeat;
    }

    &__button {
      max-width: 322px;
      width: 100%;

      &-item {
        width: 100%;
        font-weight: 600;

        box-shadow: 0px 2px 6px rgba(0, 35, 103, 0.05);

        &::after {
          content: '';
          width: 20px;
          height: 20px;

          display: block;

          margin: 0 0 0 10px;

          background: url("../img/star-empty.svg") center center/contain no-repeat;
        }

        &--active {
          border: 2px solid #FFA319;

          &::after {
            background: url("../img/star-full.svg") center center/contain no-repeat;
          }
        }
      }
    }

    &__button + &__content {
      margin: 16px 0 0 0;
    }

    &__content {
      max-width: 321px;
    }

    &__text {
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;
    }

    &--active {
      .favorite {
        &-block {
          &__content {
            display: none;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1280px) {
  .training {
    &__header {
      &-wrapper {
        gap: 0;
        grid-template-columns:auto 415px;
      }
    }

    &__text {
      &-small {
        max-height: unset;
        max-width: unset;
      }
    }

    &-info{
      &__wrapper{
        &-block{
          & + & {
            margin: 0 0 0 10px;
          }
        }
      }
    }

    &-banner {
      &__wrapper {
        &-content {
          margin: 0 0 0 30px;
        }
      }
    }

    &-tags {
      &__wrapper {
        &-item {
          & + & {
            margin: 0 0 0 15px;
          }
        }
      }
    }

    &-header {
      &__favorite {
        flex-direction: column;

        &-btn {
          margin: 24px 0 0 0;
          padding: 0;
        }
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .training {
    &__header {
      &-wrapper {
        grid-template-columns: minmax(0, 1fr);
      }

      &-block {
        &:nth-child(2) {
          display: none;
        }
      }

      &-img {
        margin: 24px 0 0 0;

        &_mobile {
          display: flex;
        }
      }
    }

    &__tags {
      margin: 88px 0 0 0;
    }

    &__description {
      margin: 48px 0 0 0;
    }

    &__banner {
      margin: 88px 0 0 0;
    }

    &__grid {
      grid-template-columns: minmax(0, 1fr);
      gap: 64px;
    }

    &__faq {
      margin: 88px 0 0 0;

      &-content {
        margin: 32px 0 0 0;
      }
    }

    &__lessons{
      margin: 24px 0 0 0;
    }

    &__info{
      &-grid{
        grid-template-columns: minmax(0,1fr);
        gap: 12px;
        grid-auto-rows: auto;
      }
      &-item{
        padding: 16px 24px;
      }
    }

    &-form {
      &__footer {
        margin: 16px 0 0 0;
      }
    }

    &-header {
      &__info {
        margin: 24px 0 0 0;
      }

      &__favorite {
        margin: 32px 0 0 0;
      }
    }

    &-tags {
      &__wrapper {
        display: grid;
        grid-template-columns: repeat(2, minmax(0, 1fr));
        gap: 15px;

        &-item {
          & + & {
            margin: 0;
          }
        }
      }
    }

    &-banner {
      &__social {
        display: flex;
        align-items: center;

        &-list {
          margin: 0 0 0 24px;
        }
      }

      &__wrapper {
        flex-direction: column;

        height: auto;

        padding: 40px 0;

        &-content {
          order: 1;
          margin: 0;
        }

        &-img {
          order: 2;

          transform: translateY(0) translateX(-40px);

          margin: 16px 0;

          width: calc(100% + 80px);
          height: auto;
        }

        &-social {
          order: 3;

          align-self: center;
          justify-self: center;

          padding: 0;
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .training {
    &-compound{
      &__grid{
        grid-template-columns: minmax(0,1fr);
        gap: 14px;
      }
    }
    &-info {
      &__wrapper {
        flex-direction: column;
        align-items: flex-start;
        position: relative;
        &-btn{
          width: 100%;
          .button{
            max-width: 100%;
            width: 100%;
          }
        }
        &-block {
          min-height: 40px;
          &_bonus {
            position: absolute;
            right: 0;
            top: 0;
          }

          & + & {
            margin: 16px 0 0 0;
          }

          &_bonus + & {
            margin: 0;
          }

          & + &_buttons{
            margin: 40px 0 0 0;
            width: 100%;
          }
        }
      }
    }

    &-header {
      &__favorite {
        &-btn {
          margin: 20px auto 0 auto;
        }
      }
    }

    &-tags {
      &__wrapper {
        grid-template-columns: minmax(0, 1fr);
        gap: 20px;
      }
    }

    &__img{
      &-certificate{
        &-btn{
          display: none;
        }

        img{
          width: 130px;
          height: 90px;
        }
      }
    }

    &__header{
      &-img{
        &_end{
          &::before{
            width: 118px;
            height: 118px;
          }
        }
        &_small{
          padding: 16px;
        }
      }
    }
  }
  .favorite {
    &-block {
      width: 100%;

      &__wrapper {
        width: 100%;

        padding: 16px;
      }
    }
  }
}