.form{
  &-input{
    width: 100%;
    &__item{
      -webkit-appearance: none;
      outline: none;

      padding: 24px;

      width: 100%;
      height: 100%;

      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;

      border: 1px solid #C0C6FF;
      border-radius: 12px;

      resize: none;

      &-small{
        height: 52px;
      }
      &::placeholder{
        color: #929292;
        opacity: 1;
      }
    }
  }
}